import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { logout } from '../../actions/auth-action'
import { Menu,Dropdown,Avatar, Badge,Button, Modal } from 'antd';
import { UserOutlined,ShoppingCartOutlined, LogoutOutlined, ProfileOutlined, BellOutlined } from '@ant-design/icons';
import { getInquiries } from '../../actions/inquiry-action'
import { updateCustomerSellingStatus } from '../../actions/auth-action'
import './Navbar.css';
import logo from '../../assets/logo.png'
import icon2 from "../../assets/icon2.png";
import icon3 from "../../assets/icon3.png";
import icon4 from "../../assets/icon4.png";
import Loader from "../../containers/App/Loader";

class Navbar extends Component {
    constructor(props){
        super(props);
        this.state={
            gstFileName:"",
            showModal:false
        }
    }

    componentDidMount() {
        this.props.getInquiries()
    }

    handleMenuClick = (e) => {
       if(e.key==="logout"){
           this.props.logout()
       }
       if(e.key==="sellerManagement"){
           this.props.history.push('/sellerManager');
       }
       if(e.key==="inventoryManagement"){
           this.props.history.push('/inventoryManager');
       }
       if(e.key==="inquiries"){
            this.props.history.push('/inquiries');
        }
    }

    handleOk = e => {
        console.log(e);
        this.setState({
            showModal: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            showModal: false,
        });
    };

    getPendingQueriesCount(inquiries){
        let count = 0;
        if(inquiries.inquiries){
            for(let i=0; i<inquiries.inquiries.length; i++){
                if(inquiries.inquiries[i].status=="PENDING"){
                    count++;
                }
            }
        }
        return count;
    }

    becomeSeller = (user) => {
        let payload = {
            name:user.name,
            email:user.email,
            username:user.username,
            contact:user.contact,
            isActive:user.isActive,
            status:"SELLER_PENDING",
            tinNumber:user.tinNumber,
            gstFileUrl: user.gstFileUrl,
            gstFileStatus:user.gstFileStatus,
            eligibleToSell:user.eligibleToSell,
            role:user.role
        }
        this.props.updateCustomerSellingStatus(user.userId, payload)
        this.setState({showModal:false})
    };

    render() {
        let {inquiries, auth, customers} = this.props;
        return(
            <div className={"navigation"}>
                {(auth.loading) && <Loader/>}
                <Link to={"/"}><div className={"appName"}><img src={logo} alt={"logo"}/></div></Link>
                <div className={"navRight"}>
                    <Menu mode="horizontal" style={{borderWidth:'0px'}}>
                        {this.props.auth.user && this.props.auth.user.status==="SELLER_APPROVED" && <Menu.Item key="seller">
                            <Link to={""}><div style={{backgroundColor:"#1b96f9", color:"white", borderRadius:"40px", paddingLeft:"20px",paddingRight:"20px",paddingTop:"0",paddingBottom:"0", fontSize:"0.9rem", fontWeight:"normal"}} >Seller Console</div></Link>
                        </Menu.Item>}
                        {this.props.auth.user && this.props.auth.user.status==="SELLER_PENDING" && <Menu.Item key="seller">
                            <div style={{backgroundColor:"lightgrey", color:"white", borderRadius:"40px", paddingLeft:"20px",paddingRight:"20px",paddingTop:"0",paddingBottom:"0", fontSize:"0.9rem", fontWeight:"normal"}} >Seller Request Pending</div>
                        </Menu.Item>}
                        {this.props.auth.user && this.props.auth.user.status==="PENDING" && this.props.auth.user.role!=="ADMIN" && <Menu.Item key="seller">
                            <div onClick={() => this.setState({showModal:!this.state.showModal})} style={{backgroundColor:"#1b96f9", color:"white", borderRadius:"40px", paddingLeft:"20px",paddingRight:"20px",paddingTop:"0",paddingBottom:"0", fontSize:"0.9rem", fontWeight:"normal"}}>Become a Seller</div>
                        </Menu.Item>}
                        {!this.props.auth.isAuthenticated &&
                            <Menu.Item key="login">
                                <Link to={"/login"}>Login</Link>
                            </Menu.Item>
                        }
                        {!this.props.auth.isAuthenticated && <Link to={"signup"}><Button style={{backgroundColor:"#1b96f9", color:"white"}} shape="round" size={'large'} >Signup</Button></Link>}
                        { this.props.auth.isAuthenticated &&
                            <Menu.Item key="login" >
                                <Dropdown.Button overlay={
                                    (
                                        <Menu onClick={this.handleMenuClick}>
                                            {/*{this.props.auth.user.role!=="ADMIN" &&<Menu.Item key="profile" icon={<ProfileOutlined />}>*/}
                                            {/*    My Profile*/}
                                            {/*</Menu.Item>}*/}
                                            {this.props.auth.user.role==="ADMIN" &&<Menu.Item key="inquiries" icon={<ProfileOutlined />}>
                                                Inquiries ({this.getPendingQueriesCount(inquiries)})
                                            </Menu.Item>}
                                            {this.props.auth.user.role==="ADMIN" && <Menu.Item key="sellerManagement" icon={<ProfileOutlined />}>
                                                Seller Management
                                            </Menu.Item>}
                                            {this.props.auth.user.role==="ADMIN" && <Menu.Item key="inventoryManagement" icon={<ProfileOutlined />}>
                                                Inventory Management
                                            </Menu.Item>}
                                            <Menu.Item key="logout" icon={<LogoutOutlined />}>
                                                Signout
                                            </Menu.Item>
                                        </Menu>
                                    )
                                } icon={<UserOutlined style={{ verticalAlign: 'middle', fontSize:'1.2rem', paddingLeft:'0.3rem'}}/>}>
                                    {this.props.auth.user.name}
                                </Dropdown.Button>
                            </Menu.Item>
                        }
                    </Menu>
                    <Modal
                        title=""
                        visible={this.state.showModal}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        footer={[]}
                    >
                        <div className={"becomeSellerModal"}>
                            <div className={"whatWeOfferSection"}>
                                <div className={"aboutHead"}>Why become a seller on H3 Mart?</div>
                                <div className={"aboutSubHead"}>Onboard your inventory on our platform and boost your sales from the comfort of your couch!</div>
                                <div className={"row"}>
                                    <div className={"col-md-4"}>
                                        <div className={"aboutSection"}>
                                            <div className={"aboutIcon"}>
                                                <img src={icon2} alt={"icon2"}/>
                                            </div>
                                            <div className={"aboutIconHead"}>
                                                Hassle Free Inventory Management
                                            </div>
                                        </div>
                                    </div>
                                    <div className={"col-md-4"}>
                                        <div className={"aboutSection"}>
                                            <div className={"aboutIcon"}>
                                                <img src={icon3} alt={"icon3"}/>
                                            </div>
                                            <div className={"aboutIconHead"}>
                                                We manage negotiations for you
                                            </div>
                                        </div>
                                    </div>
                                    <div className={"col-md-4"}>
                                        <div className={"aboutSection"}>
                                            <div className={"aboutIcon"}>
                                                <img src={icon4} alt={"icon4"}/>
                                            </div>
                                            <div className={"aboutIconHead"}>
                                                100% Verified Buyers
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <Button key="submit" type="primary" onClick={() => this.becomeSeller(auth.user)} style={{backgroundColor:"#1b96f9", color:"white", borderRadius:"40px", paddingLeft:"20px",paddingRight:"20px",paddingTop:"5px",paddingBottom:"5px", fontSize:"1rem", fontWeight:"normal", marginTop:'3rem', height:'auto'}}>
                                    Submit Seller Request
                                </Button>
                            </div>
                        </div>
                    </Modal>
                </div>
            </div>
        )
    }
}
Navbar.propTypes = {
    logout: PropTypes.func.isRequired,
    getInquiries:PropTypes.func.isRequired,
    updateCustomerSellingStatus:PropTypes.func.isRequired,
    inquiries:PropTypes.object,
    auth:PropTypes.object,
    customers: PropTypes.object
}

const mapStateToProps = state  => ({
    inquiries:state.inquiries,
    customers:state.customers,
    auth:state.auth
})

export default connect(mapStateToProps,{ logout, getInquiries, updateCustomerSellingStatus })(withRouter(Navbar))