import {
    BASE_URL,
    GET_SELLERS,
    GET_SELLER_BY_ID,
    UPDATE_SELLER,
    GET_USER_SELLERS,
    SELLERS_LOADING,
    SELLER_ERROR,
    PENDING_SELLERS
} from '../actions/constants'
const initialState = {
};

export default function(state = initialState, action) {
    switch(action.type) {
        case SELLERS_LOADING:
            return {
                ...state,
                loading: action.payload.loading
            }
        case GET_SELLERS:
            return {
                ...state,
                sellers: action.payload.sellers,
                loading: action.payload.loading
            };
        case SELLER_ERROR:
            return {
                ...state,
                error: action.payload.error,
                loading: action.payload.loading
            };
        case GET_SELLER_BY_ID:
            return {
                ...state,
                seller:action.payload.seller,
                loading:action.payload.loading
            }
        case PENDING_SELLERS:
            return {
                ...state,
                pendingSellers:action.payload.pendingSellers,
                loading: action.payload.loading
            }
        default:
            return state;
    }
}
